  import { getStoreCategory, getStoreStores } from "@/api/biunique"
  import CourseCard from '@/components/backgroundPromote/CourseCard'
  import List from '@/components/backgroundPromote/List'
  export default {
    components:{
      CourseCard,
      List
    },
    async asyncData(context) {
      const result = await getStoreCategory()
      let type = context.route.path === '/promote/scientificGroup' ? 2 : 1
      let params = { page: 1, pageSize: 10, type:type }
      const res = await getStoreStores(params)
      let entries = result.data.data.entries
      let arr = entries.filter(p => (p.parentId === 0))
      arr.forEach(element => {
        const itemarr = entries.filter(p => (p.parentId === element.id))
        element.child = itemarr.length ? itemarr : []
      })
      return { categoryList:arr, list:res.data.data.entries, total:+res.data.data.total, type:type }
    },
    data() {
      return {
        categoryList:[],
        activeTab:-1,
        list: [],
        loading: false,
        finished: false,
        pagination: {
          current: 1
        },
      }
    },
    mounted () {
      if(this.total > 0) {
        this.pagination.current = 2
      }
      if (this.list.length >= this.total) {
        this.finished = true
      }
    },
    methods:{
      tabClick(id) {
        this.activeTab = id
        this.pagination.current = 1
        this.getListHand()
      },
      onLoad(){
        setTimeout(() =>{
          this.getListHand()
        }, 500)
      },
      async getListHand() {
        let params = {
          page: this.pagination.current,
          pageSize: this.pagination.pageSize,
          type:this.type
        }
        if(this.activeTab !== -1) {
          params.categoryId = this.activeTab
        } else {
          params.categoryId = 0
        }
        const { data } = await getStoreStores(params)
        this.loading = false
        if(data.code === 0) {
          this.list = params.page === 1 ? data.data.entries : [...this.list, ...data.data.entries]
          this.total = +(data.data.total)
          this.pagination.current = this.pagination.current + 1
          if (this.list.length === +(data.data.total)) {
            this.finished = true
            this.loading = false
          }
        }
      }
    }
  }