import BackgroundPromoteBottom from '@/components/backgroundPromote/BackgroundPromoteBottom'
import { storage } from 'ais-common-utils'
import config from "@/config/common.config"
export default {
  components:{
    BackgroundPromoteBottom
  },
  props:{
    categoryList:{
      type:Array,
      required:true
    },
    activeTab:{
      type:Number,
      required:true
    },
    type:{
      type:Number,
      required:true
    }
  },
  methods:{
    tabClick(id) {
      this.$parent.tabClick(id)
    },
    handleClick() {
      let path = `/promoteApply?id=0`
      if( !storage.get('userStatus')){
        const url = encodeURIComponent(`${config.LINK.school_h5}${path}`)
        window.location.href = `${config.POST.school_h5}/school/passport/login?mobile=1&state=${url}`
        return
      }
      this.$router.push({
        path:path
      })
    },
  }
}
